import { useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import './LineChart.css';

const LineChart = (props) => {

    useEffect(() => {
        setTimeout(() => props.dataTick(), 100);
    })
    
    return (
        <div className='LineChart'>
            <h2 className='LineChart__title'>
                Line Chart
            </h2>
            <div className='LineChart__chart'>
                <Line
                    data={{
                        datasets: [{
                            label: 'Frequency',
                            data: props.data,
                            fill: true,
                            borderWidth: 1,
                            radius: 0,
                            borderColor: 'black',
                        }],
                    }}
                    options={{
                        animation: {
                            duration: 0
                        },
                        interaction: {
                            intersect: false
                        },
                        plugins: {
                            legend: false
                        },
                        scales: {
                            x: {
                                type: 'linear',
                                suggestedMax: 200
                            },
                            y: {
                                type: 'linear',
                                suggestedMax: 400
                            }
                        }
                    }}
                />
            </div>
            <button onClick = {() => {
                let data = [];
                let prev = 100;
                for (let i = 0; i < 10000; i++) {
                  prev += 5 - Math.random() * 10;
                  if (prev < 0) prev = 0;
                  data.push({x: i, y: prev});
                }
                props.updateData(data);
                }}>Update Data</button>
        </div>
    )
}



export default LineChart;