
const UPDATE_DATA = 'UPDATE_DATA';
const CHART_TICK = 'CHART_TICK';


let data = [];
let prev = 100;
for (let i = 0; i < 10000; i++) {
  prev += 5 - Math.random() * 10;
  if (prev < 0) prev = 0;
  data.push({x: i, y: prev});
}

const dataToCurrent = (data, currentData, currentDataIndex) => {
    let newCurrentData = [];
    if (data.length < currentDataIndex + 101){
        return currentData;
    } else {
        newCurrentData = data.slice(currentDataIndex + 1, currentDataIndex + 101);
        return newCurrentData;
    }
}

let initState = {
        data: data,
        currentData: [],
        currentDataIndex: 0
}

const mainPageReducer = (state = initState, action) => {
    switch (action.type) {
        
        case UPDATE_DATA:
            {
                debugger
                return {
                    ...state,
                    data: [state.data, action.data],
                };
            }
        case CHART_TICK:
            { 
                debugger
                return {
                    ...state,
                    currentData: dataToCurrent(state.data, state.currentData, state.currentDataIndex),
                    currentDataIndex: dataToCurrent(state.data, state.currentData, state.currentDataIndex) === state.currentData
                    ? state.currentDataIndex : state.currentDataIndex + 1
                }
            }
        default: 
            return state;
    }
}

export default mainPageReducer;

export const updateData = (data) => ({ type: UPDATE_DATA, data});
export const dataTick = () => ({ type: CHART_TICK });

